import '../css-files/container.css';
import React, {useEffect, useState} from 'react';

export const ProjectCard = ({title, subtitle, description, detail, bstyle, url, imageSrc}) => {
    return (
        <div className="project-card">
           <div className="top-border" style={bstyle}></div>

            <div className="top-container">
                <a href={url} className="top-title" target="_blank" rel="noopener noreferrer">{title}</a>
            </div>

            <div className="image-container">
                <img src={imageSrc} alt={`${title} image`} width="300px" height="150px"/>
            </div>

            <div className="bottom-container">
                
                <div className="scroll-down">
                    <span className="scroll-title"> <p> {subtitle} </p></span>
                    <span className="text-container"> <p> {description} </p></span>
                </div>
                
                <div className="scroll-up">
                    <p>{detail}</p>
                </div>
                
            </div>
        </div>
    );

}