import '../css-files/body.css';
import { ProjectCard } from './ProjectCard';
import React from 'react';
import firstImage from '../images/firstImage.png';
import secondImage from '../images/secondImage.png';
import thirdImage from '../images/thirdImage.png';
import fourthImage from '../images/fourthImage.png';
import fifthImage from '../images/fifthImage.png';
import sixthImage from '../images/sixthImage.png';

export const Body = () => {

    const firstStyle = {
        border: '5px solid #b8860b'
    };

    const secondStyle = {
        border: '5px solid #008080'
    };

    const thirdStyle = {
        border: '5px solid #186a8e'
    };

    const fourthStyle = {
        border: '5px solid #cc0000'
    };

    const fifthStyle = {
        border: '5px solid #4b0082'
    };

    const sixthStyle = {
        border: '5px solid #556b2f'
    };

    return (
        <div id="project-section" className="body">

            <div className="projects">
                <ProjectCard 
                    title="1st & 2nd Year Experience Story"
                    subtitle="1st & 2nd Year Experience Story"
                    description="Tableau story that visualizes the data for the First and Second Year Experience program at Jax State."
                    detail="First and Second Year Experience story is built in Tableau. It consists of four views: Freshman Forum and Lambda Sigma retention and graduation data benchmarked with the university wide data; Freshman Forum and Lambda Sigma participation numbers; Gamecock Orientation data; and First Generation data."
                    bstyle={firstStyle}
                    url="https://www.jsu.edu/registrar/degreeworks/"
                    imageSrc={firstImage}
                />

                <ProjectCard
                    title="Factbook"
                    subtitle="Factbook"
                    description="This is the Jax State's Factbook and consists university's aggregate data such as enrollment, retention, graduation, etc."
                    detail="Factbook is a website via which Jax State publishes its aggregate data. It consists of several Tableau stories: Admissions, Enrollment, Credit Hour Production, Academics, Employee information, etc. My boss, Blake Hunter created most of the Tableau stories. Mostly I maintain them and occassionly I create new ones."
                    bstyle={secondStyle}
                    url="https://www.jsu.edu/ire/factbook/index.html"
                    imageSrc={secondImage}
                />

                <ProjectCard
                    title="The Payroll System"
                    subtitle="The Payroll System"
                    description="This is a web based implementation of a Time, Attendance, and Payroll system. This application provides differnt Payroll related services to a company."
                    detail="This is a web based implementation of a Time, Attendance, and Payroll system using Java, Java JSP, and MySQL. This application provides following services: clock-in/clock-out terminals via which the employees can clock-in and clock-out and user login interface to view attendance and payroll information."
                    bstyle={thirdStyle}
                    url="https://github.com/sbanjara/payroll-system"
                    imageSrc={thirdImage}
                />

                <ProjectCard
                    title="Data Dictionary"
                    subtitle="Data Dictionary"
                    description="It is a simple web app via which a user can search the offical (IPEDS, SACSCOC, and AACNU) definition of terms used for federal and state level university data reporting."
                    detail="It is a simple web app built using HTML/CSS and JavaScript. For the pattern matching for search, it uses the Knuth-Morris-Pratt (KMP) algorithm."
                    bstyle={fourthStyle}
                    url="https://www.jsu.edu/ire/factbook/data-dictionary.html"
                    imageSrc={fourthImage}
                />

                <ProjectCard
                    title="Degree Works"
                    subtitle="Degree Works"
                    description="Degree Works is a web interface via which students can view their degree evaluation and plan for future terms."
                    detail="Degree Works is a software from Ellucian. I was a project manager to implement Degree Works at Jax State. Currently, I maintain it."
                    bstyle={fifthStyle}
                    url="https://www.jsu.edu/registrar/degreeworks/"
                    imageSrc={fifthImage}
                />

                <ProjectCard
                    title="Catalog"
                    subtitle="Catalog"
                    description="Catalog stores Jax State's programs, courses, and academic rules and regulations."
                    detail="I am a system administrator of catalog system. I oversee catalog creation, publication, and maintenance."
                    bstyle={sixthStyle}
                    url="https://catalog.jsu.edu/"
                    imageSrc={sixthImage}
                />
            </div>
        </div>   
    );
}