import '../css-files/contact.css';
import React, { useRef } from 'react';
import { Button, TextField, Stack } from '@mui/material';
import { useForm } from 'react-hook-form';
import emailjs from '@emailjs/browser';

export const Contact = () => {
    const handleSubmit = (e) => {
        e.preventDefault();

        emailjs.sendForm('service_22wujqi', 'template_rhazn4e', e.target, 'ieZm47KzqeydBFs0G')
        .then((result) => {
            alert("Thank you for your feedback!")
        }, (error) => {
            console.log(error.text);
        });

        e.target.reset();
    };

    return (
        <div id="contact-section" className='contact'>
            <form onSubmit={handleSubmit}>
                <div className='first-row'>
                    <TextField 
                        name="name"
                        label="Name" 
                        type="text"
                        variant='standard'
                        inputProps={{style: {color: '#FFFFFF'}}}
                        sx={{color: 'primary.main'}} />

                    <TextField 
                        name="email"
                        label="Email" 
                        type="email" 
                        variant='standard'
                        inputProps={{style: {color: '#FFFFFF'}}} />
                </div>
                <div className='row-space'></div>
                <div className='second-row'>
                    <TextField 
                        name="message"
                        label="Message or feedback" 
                        type="text"
                        variant='standard'
                        inputProps={{style: {color: '#FFFFFF'}}} />

                    <Button type="submit" variant="contained" sx={{bgcolor: '#BB86FC', width: 300}}>Submit</Button>
                </div>
            </form>
        </div>
    );
}