import '../css-files/header.css';
import React, { useState } from 'react';
import { Link } from 'react-router-dom'; 

export const Header = () => {
    const [navActive, setNavActive] = useState(false);

    const handleBurgerClick = () => {
        setNavActive(!navActive);
    };

    const handleScrollToProjects = (event) => {
        event.preventDefault();
        const projectSection = document.getElementById('project-section');
        if (projectSection) {
            projectSection.scrollIntoView({ behavior: 'smooth' });
        }
        setNavActive(false);
    };

    const handleScrollToContact = (event) => {
        event.preventDefault();
        const projectSection = document.getElementById('contact-section');
        if (projectSection) {
            projectSection.scrollIntoView({ behavior: 'smooth' });
        }
        setNavActive(false);
    };

    return (
        
        <nav>
            <div className="logo">
                <p>SB</p>
            </div>
        
            <ul className={`nav-links ${navActive ? 'nav-active' : ''}`}>
                
                <li id="test">
                    <Link to="/about" onClick={() => setNavActive(false)}>About Me</Link>
                </li>
                
                <li>
                    <a href="#project-section" onClick={handleScrollToProjects}>Work</a>
                </li>
                
                <li>
                    <a href="#contact-section" onClick={handleScrollToContact}>Contact</a>
                </li>
                
            </ul>
            
            <div className={`burger ${navActive ? 'toggle' : ''}`} onClick={handleBurgerClick}>
                <div className="line1"></div>
                <div className="line2"></div>
                <div className="line3"></div>
            </div>
        </nav>
    );
}